<template>
  <div class="w-full rounded-lg bg-lightest my-4">
    <div class="p-3">
      <p class="text-lg font-bold">Who to follow</p>
    </div>
    <div v-for="profile in profiles" :key="profile.id" class="w-full flex hover:bg-lighter p-3 border-t border-lighter">
      <img @click="$router.push(`/${profile.id}`)" :src="profile.avatar || 'default_profile.png'" class="w-12 h-12 rounded-full cursor-pointer"  alt=""/>
      <div class="hidden lg:block ml-4">
        <p @click="$router.push(`/${profile.id}`)" class="text-left text-sm font-bold leading-tight cursor-pointer">{{ profile.username }}</p>
        <p class="text-left text-sm leading-tight text-dark">{{ profile.id.slice(0, 8) }}...</p>
      </div>
      <button @click="followUser(profile.id)" class="ml-auto text-sm text-blue font-bold px-4 py-1 rounded-full border border-blue m-2">
        Follow
      </button>
    </div>
    <button class="p-3 w-full hover:bg-lighter text-left text-blue border-t border-lighter">
      Show More
    </button>
  </div>
</template>

<script>
import {mapActions, mapGetters} from "vuex";

export default {
  name: 'WhoToFollow',
  computed: {
    ...mapGetters("warpnet", {
      userProfile: "profile",
      isMySelf: "isSelf",
    }),
    ...mapGetters("profilePage", {
      profiles: "whotofollow", 
      cursor: "whotofollowCursor",
    }),
  },
  methods: {
    ...mapActions("profilePage", ["loadUsers"]),
    ...mapActions("profilePage", {
      follow: "followUser",
      unfollow: "unfollowUser",
    }),
    async followUser(profileId) {
      await this.follow(profileId).catch((err) => {
        console.error(`failed to follow [${profileId}]`, err);
      });
      await this.$store.dispatch("warpnet/loadFollowing", this.userProfile.id);
    },
    async unfollowUser(profileId) {
      await this.unfollow(profileId).catch((err) => {
        console.error(`failed to unfollow [${profileId}]`, err);
      });
      await this.$store.dispatch("warpnet/loadFollowing", this.userProfile.id);
    },
  },
  async created() {
    // Дождемся загрузки профиля пользователя перед выполнением loadUsers
    await this.$store.dispatch("authentication/loginUserIfAlreadyAuthenticated");
    
    let profileId;
    if (this.$route.name === "Home") {
      // На домашней странице используем ID текущего пользователя
      profileId = this.userProfile.id;
    } else {
      // На странице профиля используем ID из URL
      profileId = this.$route.params.id;
    }

    console.log(profileId, "???????????????????????/");
    await this.$store.dispatch("warpnet/loadUsers", {limit:20, cursor:this.cursor || "", user_id:profileId});
  },
};
</script>